/*
for blog landing page
and individual post pages
*/
.blogTitle {
  text-align: center;
  margin: 0px;
  font-family: Merienda,cursive;
  font-size: 32px;
  color: lightskyblue;
}

.blogSubTitle {
  text-align: center;
  padding-bottom: 20px;
}

.postLead {
  padding-bottom: 20px;
}

.postTitle {
  font-family: Merienda,cursive;
