}

.postIndex .actions {
  list-style: none;
}

.postPic img {
  max-width: 200px;
}

.content.postArticle p {
  margin-left: 0px;
  margin-right: 0px;
}

.postSummary {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}

@media screen and (min-width: 700px) {
  .postSummary {
    flex-wrap: nowrap;
  }
}

.postSummary h3 {
  margin: 0px;
  margin-bottom: 20px;
}

.postPublication, .postAuthorBy {
  font-size: 0.8em;
}

.postSynopsis {
  margin-left: 0px;
