    width: auto;
  }
}

.postText {
  margin-bottom: 20px;
}

/*
for top-level image
and social icons
and navigation links
and expanding right-side navigation pane when responsive
*/
.header {
  width: 100%;
  background-image: url("/assets/images/banner.JPG");
  height: 250px;
  background-position: 50% 60%;
  min-width: 330px;
}

.header > .siteTitle {
  font-family: 'Merienda', cursive;
  font-size: 32px;
  text-align: center;
  text-shadow: 2px 2px black;
  padding: 0px 15px;
  font-weight: bold;
}

.header > .siteTitle .highlight {
  color: lightskyblue;
}

.header > .siteTitle a {
  text-decoration: none;
  color: white;
}

.socialIcons {
  float: left;
  line-height: 70px;
  padding: 0 10px;
  font-size: 36px;
  text-shadow: 2px 2px black;
}

.socialIcons a {
  color: white;
  text-decoration: none;
}

.navigation.large {
  float: right;
}

@media (max-width: 992px) {
  .navigation.large {
    display: none;
  }
}

.navigation.small {
  display: none;
  position: fixed;
  right: -100%;
  top: 0;
  overflow-y: scroll;
  width: 100%;
  height: 100%;
  background: #b0b0b0;
}

.navigation.small li {
  float: none;
}

.navigation ul {
  list-style: none;
  list-style-image: none;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.navigation li {
  float: left;
  position: relative;
  display: block;
  padding: 0 20px;
  line-height: 70px;
  text-shadow: 2px 2px black;
  font-weight: bold;
}

.navigation li:hover {
  background: #2581DC;
  color: white;
}

.navigation a {
  color: white;
  text-decoration: none;
}

.nav-hamburger {
  display: none;
  position: absolute;
  top: 0;
  right: 20px;
  cursor: pointer;
  color: white;
  text-shadow: 2px 2px black;
  font-size: 36px;
  margin-right: 10px;
}

@media (max-width: 992px) {
  .nav-hamburger {
    display: block;
  }
}

.navigation-show-controls {
  position: fixed;
  top: 0;
  right: 0;
}

.navigation-slide-open .navigation.small {
  display: block;
  right: -60%;
}

.navigation-slide-open .header {
  right: 40%;
  position: relative;
}

.navigation-slide-open .banner {
  right: 40%;
  position: relative;
}

.navigation-slide-open .content {
  right: 40%;
  position: relative;
}

.navigation-slide-open .footer {
  right: 40%;
  position: relative;
}

.navigation-slide-open .navigation-show-controls {
  right: 40%;
}

.navigation-slide-open .nav-hamburger {
  right: 0px;
}

